.form-container {
  width: 320px;
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.share-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.share-title h3 {
  text-transform: uppercase;
  font-family: 'open sans';
  font-weight: 500;
  font-size: 18px;
}

.button {
  border-radius: 50%;
  width: 18px;
  height: 18px;
}

.arrow-down {
  background-image: url(../images/arrow-down.png);
  background-position: 50% 50%;
  background-size: cover;
}

.create-card-button {
  text-align: center;
  margin: auto;
}

.page_footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 110px;
}

.page_footer .footer-copyright {
  margin: auto;
  color: #6b6b6b;
  font-size: 14px;
  font-family: 'open sans';
}

.page_footer .footer-logo-container {
  margin: auto;
}

.page_footer .footer-logo-container img {
  width: 103px;
  height: 40px;
}

@media (min-width: 768px) {
  .page_footer {
    height: 60px;
    flex-direction: row;
    justify-content: space-between;
  }
}
